@font-face {
    font-family: 'OpenSans-Regular';
    src: local('OpenSans-Regular'),
        url(../../assets/fonts/OpenSans-Regular.ttf) format('truetype');
    font-weight: 400;
    font-style: normal;
}

@font-face {
